                                    </ol>
                                    <h3></h3>
                                    <p></p>
                                    <h3></h3>
                                    <ol class="pb-2">
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </ol>
                                </div>
                                <div class="mt-2 text-center">
                                    <input class="tgl tgl-flip" id="consent" type="checkbox" v-model="consent"/>
                                    <label style="display: inline-block;" class="tgl-btn mt-2 mr-2" data-tg-off="" data-tg-on="" for="consent"></label>
                                    <label style="display: inline-block;" :class="[consent ? 'text-success' : 'text-danger']" for="consent">
                                        {{ consent ? '' : '' }}
                                    </label>
                                </div>
                                <div class="buttons pt-2">
                                    <button class="next" :disabled="!consent"></button>
                                    <button class="prev" style="background-color: var(--background-secondary);"></button>
                                </div>
                            </div>
                        </div>
                    </li>
                    <!-- Step 3 -  End -->

                    <!-- Step 4 -  Start -->
                    <li ref="listItem4" class="listItem" id="step4">
                        <div class="col1">
                            <span class="step">
                                <span>4</span>
                                <span class="checkmark"></span>
                            </span>
                            <span class="line"></span>
                        </div>
                        <div ref="stepBody" class="col2 stepBody">
                            <div class="stepTitle">
                                Step 4 - 
                            </div>
                            <div class="content">
                                <div class="pt-2"
                                    v-if="final !== 'success'">
                                    <div class="row mb-5" style="max-height: 1200px;">
                                        <div class="col-md-6">
                                            <img class="rounded mx-auto d-block w-100"
                                                style="max-height: 600px;"
                                                :src="pictureSrc" />
                                        </div>
                                        <div class="col-md-6">
                                            <h3 v-if="picture !== null">{{ picture.name }}</h3>
                                            <p style="font-size: 24px;">{{ content }}</p>
                                        </div>
                                    </div>
                                    <h3 class="w-100"></h3>
                                    <div class="buttons">
                                        <button type="button"
                                            class="submit btn yes"
                                            @click="submitForm()"
                                            :disabled="final !== null">
                                            <div v-if="final === null">
                                                
                                            </div>
                                            <div v-else>
                                                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                <span class="sr-only">Loading...</span>
                                            </div>
                                        </button>
                                        <button class="prev" v-if="final === null"></button>
                                    </div>
                                </div>
                                <div v-if="final === 'success'">
                                    <h3 class="success"></h3>
